<template><section>
<h2 id="steps-bu-zou-tiao"><a class="header-anchor" href="#steps-bu-zou-tiao" aria-hidden="true">¶</a> Steps 步骤条</h2>
<p>点击返回上一层区域，回调用 <code v-pre>right-change</code> 事件</p>
<h3 id="ji-ben-yong-fa"><a class="header-anchor" href="#ji-ben-yong-fa" aria-hidden="true">¶</a> 基本用法</h3>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;yuki-steps :steps=\&quot;steps\&quot; v-model=\&quot;activeStep\&quot;&gt;&lt;/yuki-steps&gt;\n\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\nexport default {\n    data() {\n        return {\n            steps: ['第一步', '第二步', '第三步'],\n            activeStep: 0\n        }\n    },\n    mounted() {\n        setTimeout(() =&gt; {\n            this.activeStep = 2\n        }, 5000)\n    }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="show-demo" slot="source"><html><head></head><body><yuki-steps :steps="steps" v-model="activeStep"></yuki-steps>


</body></html></div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">yuki-steps</span> <span class="hljs-attr">:steps</span>=<span class="hljs-string">"steps"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"activeStep"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">yuki-steps</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">steps</span>: [<span class="hljs-string">'第一步'</span>, <span class="hljs-string">'第二步'</span>, <span class="hljs-string">'第三步'</span>],
            <span class="hljs-attr">activeStep</span>: <span class="hljs-number">0</span>
        }
    },
    mounted() {
        setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
            <span class="hljs-keyword">this</span>.activeStep = <span class="hljs-number">2</span>
        }, <span class="hljs-number">5000</span>)
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="ke-dian-ji"><a class="header-anchor" href="#ke-dian-ji" aria-hidden="true">¶</a> 可点击</h3>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;yuki-steps :steps=\&quot;steps\&quot; @change=\&quot;onChange\&quot; :active-step=\&quot;activeStep\&quot; clickable&gt;&lt;/yuki-steps&gt;\n\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\nexport default {\n    data() {\n        return {\n            steps: ['第一步', '第二步', '第三步'],\n            activeStep: 0\n        }\n    },\n    methods: {\n        onChange(value) {\n            Notification.success({\n                message: 'Step:' + value,\n                offset: 100\n            })\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="show-demo" slot="source"><html><head></head><body><yuki-steps :steps="steps" @change="onChange" :active-step="activeStep" clickable></yuki-steps>


</body></html></div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">yuki-steps</span> <span class="hljs-attr">:steps</span>=<span class="hljs-string">"steps"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"onChange"</span> <span class="hljs-attr">:active-step</span>=<span class="hljs-string">"activeStep"</span> <span class="hljs-attr">clickable</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">yuki-steps</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">steps</span>: [<span class="hljs-string">'第一步'</span>, <span class="hljs-string">'第二步'</span>, <span class="hljs-string">'第三步'</span>],
            <span class="hljs-attr">activeStep</span>: <span class="hljs-number">0</span>
        }
    },
    <span class="hljs-attr">methods</span>: {
        onChange(value) {
            Notification.success({
                <span class="hljs-attr">message</span>: <span class="hljs-string">'Step:'</span> + value,
                <span class="hljs-attr">offset</span>: <span class="hljs-number">100</span>
            })
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="zi-ding-yi-step-de-index"><a class="header-anchor" href="#zi-ding-yi-step-de-index" aria-hidden="true">¶</a> 自定义 Step 的 Index</h3>
<p><strong>推荐使用这种方式，尤其是有动态改变的步骤数量的情况</strong></p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;yuki-steps :steps=\&quot;steps1\&quot; @change=\&quot;onChange\&quot; :active-step=\&quot;activeStep1\&quot; clickable&gt;&lt;/yuki-steps&gt;\n\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\nexport default {\n    data() {\n        return {\n            steps1: [\n                {label: '第一步', id: 'one'},\n                {label: '第二步', id: 'two'},\n                {label: '第三步', id: 'three'}\n            ],\n            activeStep1: 2\n        }\n    },\n    methods: {\n        onChange(value) {\n            Notification.success({\n                message: 'Step:' + value,\n                offset: 100\n            })\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="show-demo" slot="source"><html><head></head><body><yuki-steps :steps="steps1" @change="onChange" :active-step="activeStep1" clickable></yuki-steps>


</body></html></div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">yuki-steps</span> <span class="hljs-attr">:steps</span>=<span class="hljs-string">"steps1"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"onChange"</span> <span class="hljs-attr">:active-step</span>=<span class="hljs-string">"activeStep1"</span> <span class="hljs-attr">clickable</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">yuki-steps</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">steps1</span>: [
                {<span class="hljs-attr">label</span>: <span class="hljs-string">'第一步'</span>, <span class="hljs-attr">id</span>: <span class="hljs-string">'one'</span>},
                {<span class="hljs-attr">label</span>: <span class="hljs-string">'第二步'</span>, <span class="hljs-attr">id</span>: <span class="hljs-string">'two'</span>},
                {<span class="hljs-attr">label</span>: <span class="hljs-string">'第三步'</span>, <span class="hljs-attr">id</span>: <span class="hljs-string">'three'</span>}
            ],
            <span class="hljs-attr">activeStep1</span>: <span class="hljs-number">2</span>
        }
    },
    <span class="hljs-attr">methods</span>: {
        onChange(value) {
            Notification.success({
                <span class="hljs-attr">message</span>: <span class="hljs-string">'Step:'</span> + value,
                <span class="hljs-attr">offset</span>: <span class="hljs-number">100</span>
            })
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>

<script>
import { Notification } from 'element-ui'
export default {
    data() {
        return {
            steps: ['第一步', '第二步', '第三步'],
            steps1: [
                {label: '第一步', id: 'one'},
                {label: '第二步', id: 'two'},
                {label: '第三步', id: 'three'}
            ],
            activeStep: 0,
            activeStep1: 2
        }
    },
    mounted() {
        setTimeout(() => {
            this.activeStep = 2
        }, 5000)
    },
    methods: {
        onChange(value) {
            Notification.success({
                message: 'Step:' + value,
                offset: 100
            })
        }
    }
}
</script>


