<template><section>

<h2 id="checkbox-duo-xuan-kuang"><a class="header-anchor" href="#checkbox-duo-xuan-kuang" aria-hidden="true">¶</a> Checkbox 多选框</h2>
<p>在 <code v-pre>el-checkbox</code> 根组件上加上类 <code v-pre>yuki-checkbox</code> 即可.</p>
<h3 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h3>
<p>单独使用可以表示两种状态之间的切换, 写在标签中的内容为 checkbox 按钮后的介绍.</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;el-checkbox class=\&quot;yuki-checkbox\&quot; v-model=\&quot;checked\&quot;&gt;备选项&lt;/el-checkbox&gt;\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\nexport default {\n    data() {\n        return {\n            checked: true\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="show-demo" slot="source"><html><head></head><body><el-checkbox class="yuki-checkbox" v-model="checked">备选项</el-checkbox>

</body></html></div>
                            <p>在 <code>el-checkbox</code> 元素中定义 <code>v-model</code> 绑定变量, 单一的 <code>checkbox</code> 中, 默认绑定变量的值会是 <code>Boolean</code>, 选中为 <code>true</code>.</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">checked</span>: <span class="hljs-literal">true</span>
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="duo-xuan-kuang-zu"><a class="header-anchor" href="#duo-xuan-kuang-zu" aria-hidden="true">¶</a> 多选框组</h3>
<p>适用于多个勾选框绑定到同一个数组的情景, 通过是否勾选来表示这一组选项中选中的项.</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;el-checkbox-group v-model=\&quot;checkList\&quot;&gt;\n    &lt;el-checkbox class=\&quot;yuki-checkbox\&quot; label=\&quot;复选框 A\&quot;&gt;&lt;/el-checkbox&gt;\n    &lt;el-checkbox class=\&quot;yuki-checkbox\&quot; label=\&quot;复选框 B\&quot;&gt;&lt;/el-checkbox&gt;\n    &lt;el-checkbox class=\&quot;yuki-checkbox\&quot; label=\&quot;复选框 C\&quot;&gt;&lt;/el-checkbox&gt;\n    &lt;el-checkbox class=\&quot;yuki-checkbox\&quot; label=\&quot;禁用\&quot; disabled&gt;&lt;/el-checkbox&gt;\n    &lt;el-checkbox class=\&quot;yuki-checkbox\&quot; label=\&quot;选中且禁用\&quot; disabled&gt;&lt;/el-checkbox&gt;\n&lt;/el-checkbox-group&gt;\n\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\nexport default = {\n    data() {\n        return {\n            checkList: ['选中且禁用', '复选框 A']\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="show-demo" slot="source"><html><head></head><body><el-checkbox-group v-model="checkList">
    <el-checkbox class="yuki-checkbox" label="复选框 A"></el-checkbox>
    <el-checkbox class="yuki-checkbox" label="复选框 B"></el-checkbox>
    <el-checkbox class="yuki-checkbox" label="复选框 C"></el-checkbox>
    <el-checkbox class="yuki-checkbox" label="禁用" disabled></el-checkbox>
    <el-checkbox class="yuki-checkbox" label="选中且禁用" disabled></el-checkbox>
</el-checkbox-group>


</body></html></div>
                            <p><code>checkbox-group</code> 元素能把多个 checkbox 管理为一组, 只需要在 Group 中使用 <code>v-model</code> 绑定 <code>Array</code> 类型的变量即可. <code>el-checkbox</code> 的 <code>label</code> 属性是该 checkbox 对应的值, 若该标签中无内容, 则该属性也充当 checkbox 按钮后的介绍. <code>label</code> 与数组中的元素值相对应, 如果存在指定的值则为选中状态, 否则为不选中.</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkList"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"复选框 A"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"复选框 B"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"复选框 C"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"禁用"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选中且禁用"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox-group</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> = {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">checkList</span>: [<span class="hljs-string">'选中且禁用'</span>, <span class="hljs-string">'复选框 A'</span>]
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="indeterminate-zhuang-tai"><a class="header-anchor" href="#indeterminate-zhuang-tai" aria-hidden="true">¶</a> Indeterminate 状态</h3>
<p><code v-pre>indeterminate</code> 属性用以表示 checkbox 的不确定状态, 一般用于实现全选的效果.</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;el-checkbox class=\&quot;yuki-checkbox\&quot; :indeterminate=\&quot;isIndeterminate\&quot; v-model=\&quot;checkAll\&quot; @change=\&quot;handleCheckAllChange\&quot;&gt;全选&lt;/el-checkbox&gt;\n&lt;div style=\&quot;margin:15px 0\&quot;&gt;&lt;/div&gt;\n&lt;el-checkbox-group v-model=\&quot;checkedCities\&quot; @change=\&quot;handleCheckedCitiesChange\&quot;&gt;\n    &lt;el-checkbox class=\&quot;yuki-checkbox\&quot; v-for=\&quot;city in cities\&quot; :label=\&quot;city\&quot; :key=\&quot;city\&quot;&gt;&lt;/el-checkbox&gt;\n&lt;/el-checkbox-group&gt;\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\nconst cityOptions = ['上海','北京','广州','深圳']\nexport default = {\n    data() {\n        return {\n            checkAll: false,\n            checkedCities: ['上海', '北京'],\n            cities: cityOptions,\n            isIndeterminate: true,\n        }\n    },\n    methods:{\n        handleCheckAllChange(val) {\n            this.checkedCities = val ? cityOptions : [];\n            this.isIndeterminate = false;\n        },\n        handleCheckedCitiesChange(value) {\n            let checkedCount = value.length;\n            this.checkAll = checkedCount === this.cities.length\n            this.isIndeterminate = checkedCount &gt; 0 &amp;&amp; checkedCount &lt; this.cities.length\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="show-demo" slot="source"><html><head></head><body><el-checkbox class="yuki-checkbox" :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
<div style="margin:15px 0"></div>
<el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
    <el-checkbox class="yuki-checkbox" v-for="city in cities" :label="city" :key="city"></el-checkbox>
</el-checkbox-group>

</body></html></div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">:indeterminate</span>=<span class="hljs-string">"isIndeterminate"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkAll"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"handleCheckAllChange"</span>&gt;</span>全选<span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin:15px 0"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkedCities"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"handleCheckedCitiesChange"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"city in cities"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"city"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"city"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">const</span> cityOptions = [<span class="hljs-string">'上海'</span>,<span class="hljs-string">'北京'</span>,<span class="hljs-string">'广州'</span>,<span class="hljs-string">'深圳'</span>]
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> = {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">checkAll</span>: <span class="hljs-literal">false</span>,
            <span class="hljs-attr">checkedCities</span>: [<span class="hljs-string">'上海'</span>, <span class="hljs-string">'北京'</span>],
            <span class="hljs-attr">cities</span>: cityOptions,
            <span class="hljs-attr">isIndeterminate</span>: <span class="hljs-literal">true</span>,
        }
    },
    <span class="hljs-attr">methods</span>:{
        handleCheckAllChange(val) {
            <span class="hljs-keyword">this</span>.checkedCities = val ? cityOptions : [];
            <span class="hljs-keyword">this</span>.isIndeterminate = <span class="hljs-literal">false</span>;
        },
        handleCheckedCitiesChange(value) {
            <span class="hljs-keyword">let</span> checkedCount = value.length;
            <span class="hljs-keyword">this</span>.checkAll = checkedCount === <span class="hljs-keyword">this</span>.cities.length
            <span class="hljs-keyword">this</span>.isIndeterminate = checkedCount &gt; <span class="hljs-number">0</span> &amp;&amp; checkedCount &lt; <span class="hljs-keyword">this</span>.cities.length
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="ke-xuan-xiang-mu-shu-liang-de-xian-zhi"><a class="header-anchor" href="#ke-xuan-xiang-mu-shu-liang-de-xian-zhi" aria-hidden="true">¶</a> 可选项目数量的限制</h3>
<p>使用 <code v-pre>min</code> 和 <code v-pre>max</code> 属性能够限制可以被勾选的项目的数量.</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;el-checkbox-group v-model=\&quot;checkedCities\&quot; @change=\&quot;handleCheckedCitiesChange\&quot; :min=\&quot;1\&quot; :max=\&quot;2\&quot;&gt;\n    &lt;el-checkbox class=\&quot;yuki-checkbox\&quot; v-for=\&quot;city in cities\&quot; :label=\&quot;city\&quot; :key=\&quot;city\&quot;&gt;&lt;/el-checkbox&gt;\n&lt;/el-checkbox-group&gt;\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\nconst cityOptions = ['上海','北京','广州','深圳']\nexport default = {\n    data() {\n        return {\n            checkedCities: ['上海', '北京'],\n            cities: cityOptions,\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="show-demo" slot="source"><html><head></head><body><el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange" :min="1" :max="2">
    <el-checkbox class="yuki-checkbox" v-for="city in cities" :label="city" :key="city"></el-checkbox>
</el-checkbox-group>

</body></html></div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkedCities"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"handleCheckedCitiesChange"</span> <span class="hljs-attr">:min</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">:max</span>=<span class="hljs-string">"2"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"city in cities"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"city"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"city"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">const</span> cityOptions = [<span class="hljs-string">'上海'</span>,<span class="hljs-string">'北京'</span>,<span class="hljs-string">'广州'</span>,<span class="hljs-string">'深圳'</span>]
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> = {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">checkedCities</span>: [<span class="hljs-string">'上海'</span>, <span class="hljs-string">'北京'</span>],
            <span class="hljs-attr">cities</span>: cityOptions,
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>
<style>
.demo-check-box .source{
    position: relative;
}
.demo-check-box .yuki-checkbox{
    margin-bottom: 10px;
}
.demo-box.demo-alert .el-alert{
    margin: 20px 0 0;
}
.demo-box.demo-alert .el-alert:first-child{
    margin: 0;
}
</style>
<script>
const cityOptions = ['上海','北京','广州','深圳']
module.exports = {
    data() {
        return {
            checked: true,
            checkList: ['选中且禁用', '复选框 A'],
            checkAll: false,
            checkedCities: ['上海', '北京'],
            cities: cityOptions,
            isIndeterminate: true,
        }
    },
    methods:{
        handleCheckAllChange(val) {
            this.checkedCities = val ? cityOptions : [];
            this.isIndeterminate = false;
        },
        handleCheckedCitiesChange(value) {
            let checkedCount = value.length;
            this.checkAll = checkedCount === this.cities.length
            this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length
        }
    }
}
</script>


